#currententry {
  z-index: 999 !important;
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: #f8f8f8;
  border-top: 4px solid #e7e7e7;
  border-left: 4px solid #e7e7e7;
  border-right: 4px solid #e7e7e7;
  border-radius: 3px;
  min-height: 20px;
  min-width: 20px;
  margin-left: 43px;
  margin-right: 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2); }
  #currententry button span {
    display: none; }
  #currententry #cb-ce-msg {
    padding: 15px; }
  #currententry #cb-ce-entry {
    display: none;
    font-size: 9px; }
    #currententry #cb-ce-entry #cb-ce-entry-menu {
      background-color: #428BCA;
      padding: 5px; }
      #currententry #cb-ce-entry #cb-ce-entry-menu #cb-ce-undock, #currententry #cb-ce-entry #cb-ce-entry-menu #cb-ce-minimize {
        color: black; }
      #currententry #cb-ce-entry #cb-ce-entry-menu .cb-ce-entry-ctrls {
        float: right; }
    #currententry #cb-ce-entry #cb-ce-entry-items {
      padding: 0 20px 0 10px;
      position: relative;
      max-height: 200px;
      overflow-y: auto; }
    #currententry #cb-ce-entry #cb-ce-entry-xml {
      display: none; }
    #currententry #cb-ce-entry #cb-ce-entry-btns {
      float: right;
      padding-bottom: 5px;
      margin-right: 5px; }
    #currententry #cb-ce-entry .cb-ce-ctrls {
      position: absolute;
      left: 5px;
      cursor: pointer; }
    #currententry #cb-ce-entry ul {
      list-style: none;
      padding-left: 1em;
      word-break: break-all; }
    #currententry #cb-ce-entry ul ul {
      padding-left: 1.5em; }
    #currententry #cb-ce-entry #cb-ce-g-dd .cb-selected {
      background-color: #AFCCE6; }

@media (min-width: 500px) {
  #currententry {
    min-width: 300px;
    min-height: 2em;
    margin-left: 150px;
    z-index: 999 !important; }
    #currententry #cb-ce-entry {
      font-size: 15px; }
    #currententry button span {
      display: inline; } }

#currententry.cb-ce-minimized #cb-ce-entry-body, #currententry.cb-ce-minimized #cb-ce-entry-btns, #currententry.cb-ce-minimized .cb-ce-entry-ctrls {
  display: none; }
